var React = require('react');
var Autosuggest = require('react-autosuggest');

const suburbs = ['Cheltenham', 'Mill Park', 'Mordialloc', 'Nunawading'];

var AutoInput = React.createClass({
    getSuggestion:function(input, callback) {
        var regex = new RegExp('^' + input, 'i');
        var suggestions = suburbs.filter(function(suburb) {
            return regex.test(suburb);
        });

        callback(null, suggestions);
    },
    render :function() {
        return <Autosuggest suggestions={this.getSuggestion} />;
    }
});

module.exports=AutoInput;